import React, { ReactElement } from "react"
import { Box } from "../../ui/box"
import { Formik } from "formik"
import { Text } from "../../ui/text"
import { t } from "../../i18n/i18n"
import { HealthcareInfoFields } from "./healthcare-info-fields"
import { HealthInfoSchema } from "../../validation-schema/health-info.schema"
import { useHistory } from "react-router-dom"
import { payment } from "../../routes"

interface Props {
  total: string
}

interface HealthcareInfoProps {
  date: string
  name: string
  city: string
  address: string
  deliveryAddress: string
  email: string
  phone: string
}

const initialValues: HealthcareInfoProps = {
  date: "",
  name: "",
  city: "",
  address: "",
  deliveryAddress: "",
  email: "",
  phone: "",
}

export const HealthcareInfoForm = ({ total }: Props): ReactElement => {
  const history = useHistory()

  const handleSubmit = (values: HealthcareInfoProps): void => {
    history.push(payment, {
      name: values.name,
    })
  }

  return (
    <Box>
      <Text
        fontWeight="bold"
        fontSize={4}
        lineHeight="32px"
        ml={[0, 0, "279px"]}
        px={["70px", 0]}
        my="30px"
        textAlign={["center", "center", "left"]}
      >
        {t("general.total", {
          0: total,
        })}
      </Text>

      <Formik
        initialValues={initialValues}
        onSubmit={handleSubmit}
        validationSchema={HealthInfoSchema}
        component={HealthcareInfoFields}
      />
    </Box>
  )
}
