import React, { ReactElement } from "react"
import { Box } from "../ui/box"
import { HeaderInfoForm } from "../ui/header-info-form"
import { Text } from "../ui/text"
import { useLocation } from "react-router-dom"
import { SvgShield } from "../ui/icons/shield"
import { theme } from "../theme/theme"
import { SvgYearsOld } from "../ui/icons/years-old"
import { SvgPackage } from "../ui/icons/package"
import { SvgOptions } from "../ui/icons/options"
import styled from "styled-components"
import { HealthcareInfoForm } from "./ui/healthcare-info-form"

const StyledBox = styled(Box)`
  display: flex;
  align-items: center;
  margin-left: 20px;
  @media screen and (max-width: ${theme("breakpoints.2")}) {
    width: 100%;
  }
`

const StyledText = styled(Text)`
  font-size: 14px;
  line-height: 22px;
  margin-left: 11px;
  margin-top: 8px;
  margin-bottom: 8px;
`

export const HealthcareFormPage = (): ReactElement => {
  const location: any = useLocation()

  const { nameInsurance, packageName, years, options, total } = location.state

  return (
    <Box pb="100px">
      <HeaderInfoForm />

      <Box
        bg="blues.4"
        width="100%"
        display="flex"
        flexWrap={["wrap", "wrap", "wrap", "no-wrap"]}
        pl={[0, 0, 0, 0, "200px", "260px"]}
      >
        <StyledBox>
          <SvgShield fill={theme("colors.primary")} />
          <StyledText>{nameInsurance}</StyledText>
        </StyledBox>

        <StyledBox>
          <SvgYearsOld />
          <StyledText>{years}</StyledText>
        </StyledBox>

        <StyledBox>
          <SvgPackage fill={theme("colors.primary")} />
          <StyledText>{packageName}</StyledText>
        </StyledBox>

        <StyledBox>
          <SvgOptions />
          <StyledText>{options.slice(0, 4).join(", ")}</StyledText>
        </StyledBox>
      </Box>

      <HealthcareInfoForm total={total} />
    </Box>
  )
}
