import React, { ReactElement, useState } from "react"
import { Form, FormikProps } from "formik"
import { t } from "../../i18n/i18n"
import styled from "styled-components"
import { theme } from "../../theme/theme"

import { Box } from "../../ui/box"
import { SecondStepCheck } from "./second-step-check"
import { SecondStepButtons } from "./second-step-buttons"
import { SecondStepFirstForm } from "./second-step-first-form"
import { SecondStepSecoondForm } from "./second-step-second-form"

export enum Forms {
  PERSONAL = "PERSONAL",
  VOLUNTARY = "VOLUNTARY",
}

const StyledForm = styled(Form)`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: calc(100% - 183px);

  @media (min-width: ${theme("breakpoints.1")}) {
    height: calc(100% - 231px);
  }
`

export interface Step2Values {
  personalAmount: string | undefined
  personalNumber: string | undefined
  voluntaryAmount: string | undefined
}

export const SecondStepForm = ({
  setFieldValue,
  values,
}: FormikProps<Step2Values>): ReactElement => {
  const [openedForms, setOpenedForms] = useState<Forms[]>([])

  const isChecked = (form: Forms): boolean => openedForms.includes(form)

  const toggleForm = (formToToggle: Forms): void => {
    if (isChecked(formToToggle)) {
      setOpenedForms(
        openedForms.filter((form): boolean => form !== formToToggle),
      )

      return
    }

    setOpenedForms([...openedForms, formToToggle])
  }

  return (
    <StyledForm>
      <Box
        display="flex"
        flexDirection={["column", "column", "row"]}
        p={[
          "30px 31px",
          "30px 31px",
          "20px 120px",
          "20px 120px 20px 279px",
          "20px 279px",
        ]}
      >
        <Box
          width={["auto", "345px", "345px", "435px"]}
          mb="5px"
          mr={[0, 0, "15px"]}
        >
          <SecondStepCheck
            form={Forms.PERSONAL}
            isChecked={isChecked(Forms.PERSONAL)}
            toggleForm={toggleForm}
            text={t("carLiability.step2.personalInsurance")}
            isFirst
          />

          {isChecked(Forms.PERSONAL) && (
            <SecondStepFirstForm
              values={values}
              setFieldValue={setFieldValue}
            />
          )}
        </Box>

        <Box width={["auto", "345px", "345px", "435px"]}>
          <SecondStepCheck
            form={Forms.VOLUNTARY}
            isChecked={isChecked(Forms.VOLUNTARY)}
            toggleForm={toggleForm}
            text={t("carLiability.step2.voluntaryInsurance")}
            isLast
          />

          {isChecked(Forms.VOLUNTARY) && (
            <SecondStepSecoondForm
              values={values}
              setFieldValue={setFieldValue}
            />
          )}
        </Box>
      </Box>

      <SecondStepButtons />
    </StyledForm>
  )
}
