import React, { ReactElement } from "react"
import { Formik } from "formik"
import { useMediaQuery } from "react-responsive"
import { useLocation, useHistory } from "react-router-dom"

import { StepForm } from "../../ui/step-form"
import { InputField } from "../../ui/inputs/input-field"
import { Box } from "../../ui/box"
import { Text } from "../../ui/text"
import { theme } from "../../theme/theme"
import { t } from "../../i18n/i18n"
import { ComprehensiveCarPriceSchema } from "../../validation-scheme/comprehensive-car-price.schema"
import { comprehensiveCarExtendedTerms } from "../routes"

const initialValues = {
  price: "",
}

export const ComprehensiveCarPriceForm = (): ReactElement => {
  const isMobile = useMediaQuery({
    maxWidth: theme("breakpoints.3"),
  })
  const location = useLocation<any>()
  const { brand, model, year } = location.state
  const history = useHistory()

  const handleSubmit = (values: any) => {
    history.push(comprehensiveCarExtendedTerms, { valueByMarket: values.price })
  }

  return (
    <Formik
      initialValues={initialValues}
      onSubmit={handleSubmit}
      validationSchema={ComprehensiveCarPriceSchema}
      validateOnBlur={false}
      validateOnChange={false}
    >
      {() => (
        <StepForm>
          <Box
            display="flex"
            flexDirection={isMobile ? "column" : "row"}
            justifyContent="space-between"
          >
            <Text
              fontSize={4}
              fontWeight="800"
              lineHeight="28px"
              mt="0px"
              mb={isMobile ? "14px" : 0}
            >
              {`${brand}, ${model}, ${year}`}
            </Text>
            <Box width="100%" maxWidth="434px">
              <Text lineHeight="28px" fontSize={4} mt={0} mb="15px">
                {isMobile
                  ? t("comprehensiveCar.valueByMarketMobile")
                  : t("comprehensiveCar.valueByMarket")}
                :
              </Text>
              <Box width="100%">
                <InputField
                  placeholder={t("comprehensiveCar.valueByMarketPlaceholder")}
                  name="price"
                />
              </Box>
            </Box>
          </Box>
        </StepForm>
      )}
    </Formik>
  )
}
