import React, { ReactElement } from "react"
import { Box, BoxProps } from "./box"
import { TariffCard } from "./tariff-card"
import { Indexer } from "../core"
import { useMediaQuery } from "react-responsive"
import { theme } from "../theme/theme"

type Props = BoxProps & {
  tariffs: Indexer[]
  isHealth?: boolean
}

export const TariffCards = ({
  tariffs,
  isHealth,
  ...rest
}: Props): ReactElement => {
  const isMobile = useMediaQuery({
    maxWidth: theme("breakpoints.3"),
  })

  return (
    <Box
      display="flex"
      mx={isMobile ? 0 : "182px"}
      justifyContent="center"
      flexWrap="wrap"
      {...rest}
    >
      {tariffs.map((tariff: Indexer, index: number) => {
        const fields: Indexer = {}

        for (const key in tariff) {
          if (key === "rating" || key === "total" || key === "imagePath") {
            continue
          }

          fields[key] = tariff[key]
        }

        return (
          <TariffCard
            key={index}
            imagePath={String(tariff.imagePath)}
            rating={Number(tariff.rating)}
            fields={fields}
            total={String(tariff.total)}
            isHealth={isHealth}
          />
        )
      })}
    </Box>
  )
}
