import React, { ReactElement } from "react"
import { Box } from "../../ui/box"
import { Item, DescriptionItem } from "./description-item"
import { getImagePath } from "../../hooks/retina-image"

const items: Item[] = [
  { src: getImagePath("images/search.png"), text: "description.search" },
  {
    src: getImagePath("images/compare.png"),
    text: "description.compare",
    containerProps: { pl: [0, 0, "70px"] },
  },
  {
    src: getImagePath("images/promotions.png"),
    text: "description.promotions",
    containerProps: { pl: [0, 0, "140px"] },
  },
]

export const DescriptionList = (): ReactElement => (
  <Box
    pl={["30px", "30px", 0]}
    pr="30px"
    ml={[0, 0, "auto"]}
    mt={[0, 0, 0, 0, "57px"]}
    width={["auto", "auto", "48%", "40%"]}
  >
    {items.map(
      ({ src, text, containerProps }): ReactElement => (
        <DescriptionItem
          src={src}
          text={text}
          containerProps={containerProps}
          key={text}
        />
      ),
    )}
  </Box>
)
