import React, { ReactElement } from "react"
import { Box } from "../../ui/box"
import { Select } from "../../ui/select/select"
import { Button } from "../../ui/buttons/button"
import { t } from "../../i18n/i18n"
import { SelectItem } from "../../core"

interface Props {
  isMobile: boolean
  setFieldValue: (
    field: string,
    value: string,
    shouldValidate?: boolean,
  ) => void
}

const items = [
  { id: "1", name: "test value1" },
  { id: "2", name: "test value2" },
  { id: "3", name: "test value3" },
]

export const ComprehensiveCarHeaderFields = ({
  isMobile,
  setFieldValue,
}: Props): ReactElement => (
  <Box mt="22px" display="flex" flexDirection="column" alignItems="end">
    <Select
      id="select-brand"
      name="brand"
      items={items}
      zIndex={3}
      onChange={(selectedItem: SelectItem): void =>
        setFieldValue("brand", selectedItem.id)
      }
      inputProps={{
        placeholder: t("comprehensiveCar.carBrand"),
        fontSize: 3,
        lineHeight: "34px",
      }}
    />

    <Select
      id="select-model"
      name="model"
      items={items}
      my="16px"
      zIndex={2}
      onChange={(selectedItem: SelectItem): void =>
        setFieldValue("model", selectedItem.id)
      }
      inputProps={{
        placeholder: t("comprehensiveCar.model"),
        fontSize: 3,
        lineHeight: "34px",
      }}
    />

    <Select
      id="select-year"
      name="year"
      items={items}
      onChange={(selectedItem: SelectItem): void =>
        setFieldValue("year", selectedItem.id)
      }
      inputProps={{
        placeholder: t("comprehensiveCar.year"),
        fontSize: 3,
        lineHeight: "34px",
      }}
    />

    <Button
      type="submit"
      text={isMobile ? t("general.compare") : t("general.continue")}
      width={isMobile ? "100%" : "185px"}
      mt="15px"
      ml="auto"
    />
  </Box>
)
