import React, { ReactElement, useState } from "react"
import { Box, BoxProps } from "./box"
import { Text } from "./text"
import styled from "styled-components"
import Cookies from "js-cookie"
import { Langs } from "../core/enums"

type Props = BoxProps & {
  isMobile: boolean
}

const Root = styled(Box)`
  @media screen and (max-width: 1380px) {
    margin-left: 30px;
  }
`

export const LangSelect = ({ isMobile, ...rest }: Props): ReactElement => {
  const [lang] = useState(Cookies.get("lang") || Langs.EN)

  const handleChangeLang = (): void => {
    const newLang = lang === Langs.EN ? Langs.VI : Langs.EN

    Cookies.set("lang", newLang)

    window.location.reload()
  }

  return (
    <Root
      display="flex"
      alignItems="center"
      ml={isMobile ? 0 : "58px"}
      mr={isMobile ? "40px" : 0}
    >
      <Text mr="2px" onClick={handleChangeLang}>
        {lang}
      </Text>

      <svg
        width="7"
        height="4"
        viewBox="0 0 7 4"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M6.55925 0H0.440754C0.268922 0 0.177086 0.202384 0.290238 0.3317L3.34948 3.82798C3.42917 3.91905 3.57083 3.91905 3.65052 3.82798L6.70976 0.331701C6.82291 0.202384 6.73108 0 6.55925 0Z"
          fill="#3E3D3D"
        />
      </svg>
    </Root>
  )
}
