import * as yup from "yup"
import { MIN_RANGE, MAX_RANGE } from "../core/constants"
import { t } from "../i18n/i18n"

export const HealthHeaderSchema = yup.object().shape({
  min: yup
    .number()
    .min(MIN_RANGE)
    .max(MAX_RANGE)
    .required(),
  max: yup
    .number()
    .min(MIN_RANGE)
    .max(MAX_RANGE)
    .required(),
  date: yup.string().required(t("general.required")),
})
