import React, { ReactElement } from "react"
import { Button } from "../../ui/buttons/button"

import { t } from "../../i18n/i18n"
import { Select } from "../../ui/select/select"
import { Form, FormikProps } from "formik"
import { Box } from "../../ui/box"
import { CarLiabilityHeaderProps } from "./header-form"
import { SelectItem } from "../../core"

const purposeItems = [
  {
    id: "personal",
    name: t("carLiability.personal"),
    value: "personal",
  },
  {
    id: "commercial",
    name: t("carLiability.commercial"),
    value: "commercial",
  },
  {
    id: "trucks",
    name: t("carLiability.trucks"),
    value: "trucks",
  },
]

const typeItems = [
  { id: "Sedan", name: t("carLiability.sedan"), value: "sedan" },
  { id: "SUV", name: t("carLiability.SUV"), value: "SUV" },
  { id: "coupe", name: t("carLiability.coupe"), value: "coupe" },
  {
    id: "convertible",
    name: t("carLiability.convertible"),
    value: "convertible",
  },
  { id: "pickup", name: t("carLiability.pickup"), value: "pickup" },
]

const seatsItems = [
  { id: "2", name: `2 ${t("carLiability.seatsNumber")}`, value: "2" },
  { id: "5", name: `5 ${t("carLiability.seatsNumber")}`, value: "5" },
]

export const HeaderFormContent = ({
  setFieldValue,
}: FormikProps<CarLiabilityHeaderProps>): ReactElement => (
  <Box
    maxWidth={["300px", "300px", "300px", "436px"]}
    mx="auto"
    textAlign="left"
  >
    <Form>
      <Select
        id="purpose-select"
        name="purpose"
        items={purposeItems}
        inputProps={{ placeholder: t("carLiability.purpose"), fontSize: 3 }}
        mt={["34px", "34px", "28px"]}
        onChange={(item: SelectItem | null): void =>
          (item && setFieldValue("purpose", item.value)) || undefined
        }
      />

      <Select
        name="type"
        id="type-select"
        items={typeItems}
        inputProps={{ placeholder: t("carLiability.type"), fontSize: 3 }}
        mt="15px"
        onChange={(item: SelectItem | null): void =>
          (item && setFieldValue("type", item.value)) || undefined
        }
      />

      <Select
        id="seats-select"
        name="seats"
        items={seatsItems}
        inputProps={{ placeholder: t("carLiability.seats"), fontSize: 3 }}
        mt="15px"
        mb={["20px", "20px", "16px"]}
        onChange={(item: SelectItem | null): void =>
          (item && setFieldValue("seats", item.value)) || undefined
        }
      />

      <Button
        text={t("carLiability.action")}
        variant="primary"
        type="submit"
        display="block"
        width={["100%", "100%", "100%", "182px"]}
        py="8px"
        ml={[0, 0, "auto"]}
      />
    </Form>
  </Box>
)
