import React, { ReactElement } from "react"
import styled from "styled-components"

import { Box } from "../../ui/box"
import { theme } from "../../theme/theme"
import { getImagePath } from "../../hooks/retina-image"

const Backdrop = styled.img`
  position: absolute;
  bottom: 0;
  width: 100%;
`

const StyledImage = styled.img`
  position: relative;
  width: 190px;
  bottom: -2px;
  left: calc(50% - 95px);
  background-color: transparent;
  @media (min-width: 375px) {
    width: 223px;
    left: calc(50% - 111px);
  }
  @media (min-width: 414px) {
    left: 96px;
  }
  @media (min-width: ${theme("breakpoints.1")}) {
    width: 180px;
    left: 330px;
  }
  @media (min-width: ${theme("breakpoints.4")}) {
    width: 273px;
    left: 490px;
  }
`

export const HeaderImage = (): ReactElement => (
  <Box
    position="relative"
    maxWidth={["414px", "414px", "570px", "570px", "570px", "801px"]}
    mt={["69px", "69px", 0, 0, 0, "-72px"]}
  >
    <picture>
      <source
        media={`(min-width: ${theme("breakpoints.1")})`}
        srcSet="images/car-liability-backdrop-desktop.svg"
      />
      <Backdrop src="images/car-liability-backdrop.svg" />
    </picture>
    <StyledImage src={getImagePath("images/car-liability-header.png")} />
  </Box>
)
