import React, { ReactElement } from "react"
import { Box } from "../../ui/box"
import { HeaderContent } from "../../ui/header-content"
import { HeaderImage } from "./header-image"
import { useMediaQuery } from "react-responsive"
import { theme } from "../../theme/theme"

export const Header = (): ReactElement => {
  const isNotMobile = useMediaQuery({ minWidth: theme("breakpoints.1") })

  return (
    <Box width={["auto", "auto", "auto", "auto", "auto", "801px"]}>
      <HeaderContent
        titleText="header.carLiabilityTitle"
        buttonText={isNotMobile ? "" : "header.carLiabilityAction"}
        boxStyle={{ pt: ["5px", "5px", "35px"] }}
        titleStyle={{ width: ["auto", "auto", "334px"] }}
        textStyle={{ width: ["auto", "auto", "265px"] }}
        buttonStyle={{
          display: "block",
          maxWidth: ["300px", "171px"],
          mx: ["auto", 0],
        }}
      />
      <HeaderImage />
    </Box>
  )
}
