import React, { ReactElement } from "react"
import { t } from "../../i18n/i18n"
import { Step2Values } from "./second-step-form"
import { SelectItem } from "../../core"
import { findSelectedValue } from "../../core/utils"

import { Box } from "../../ui/box"
import { Select } from "../../ui/select/select"
import { SecondStepSelectNote } from "./second-step-select-note"

interface Props {
  values: Step2Values
  setFieldValue: (field: string, value: string | undefined) => void
}

const personalAmountItems = [
  {
    id: "20",
    name: t("carLiability.step2.personalAmount", { 0: 20 }),
    value: "20",
  },
  {
    id: "40",
    name: t("carLiability.step2.personalAmount", { 0: 40 }),
    value: "40",
  },
  {
    id: "60",
    name: t("carLiability.step2.personalAmount", { 0: 60 }),
    value: "60",
  },
]

const personalNumberItems = [
  { id: "1", name: t("carLiability.step2.participant", { 0: 1 }), value: "1" },
  { id: "2", name: t("carLiability.step2.participants", { 0: 2 }), value: "2" },
  { id: "3", name: t("carLiability.step2.participants", { 0: 3 }), value: "3" },
]

export const SecondStepFirstForm = ({
  values,
  setFieldValue,
}: Props): ReactElement => (
  <Box>
    <Select
      name="personalAmount"
      id="personal-amount-select"
      items={personalAmountItems}
      inputProps={{ fontSize: 3 }}
      onChange={(item: SelectItem | null): void =>
        item ? setFieldValue("personalAmount", item.value) : undefined
      }
      selectedValue={findSelectedValue(
        values.personalAmount,
        personalAmountItems,
      )}
    />
    <SecondStepSelectNote />

    <Select
      name="personalNumber"
      id="personal-number-select"
      items={personalNumberItems}
      inputProps={{
        fontSize: 3,
        placeholder: t("carLiability.step2.personalNumberPlaceholder"),
      }}
      mt="22px"
      onChange={(item: SelectItem | null): void =>
        item ? setFieldValue("personalNumber", item.value) : undefined
      }
    />
  </Box>
)
