export const getImagePath = (path: string): string => {
  const isRetina =
    window.devicePixelRatio > 1 ||
    (window.matchMedia &&
      window.matchMedia(
        "(-webkit-min-device-pixel-ratio: 1.5),(-moz-min-device-pixel-ratio: 1.5),(min-device-pixel-ratio: 1.5)",
      ).matches)

  if (isRetina) {
    return path.replace(".png", "@2x.png")
  }

  return path
}
