import React, { ReactElement } from "react"
import { Box } from "../ui/box"
import { useLocation } from "react-router-dom"
import { HeaderPage } from "../ui/header.page"
import { Footer } from "../ui/footer/footer"
import { t } from "../i18n/i18n"
import { Text } from "../ui/text"
import { SvgMoney } from "../ui/icons/money"
import { SvgYearsOld } from "../ui/icons/years-old"
import { TariffCards } from "../ui/tariff-cards"
import { companiesTariffs } from "../fake-data"
import { useMediaQuery } from "react-responsive"
import { theme } from "../theme/theme"

export const HealthCompanies = (): ReactElement => {
  const isMobile = useMediaQuery({
    maxWidth: theme("breakpoints.3"),
  })

  const location: any = useLocation()
  const { min, max, date } = location.state

  const age = Math.abs(
    new Date(Date.now() - new Date(date).getTime()).getUTCFullYear() - 1970,
  )

  return (
    <Box>
      <HeaderPage />

      <Text
        fontWeight="900"
        fontSize={7}
        lineHeight="48px"
        color="primary"
        ml={isMobile ? 0 : "185px"}
        textAlign={isMobile ? "center" : "left"}
        mt="40px"
        mb="30px"
      >
        {t("navigation.health")}
      </Text>

      <Box bg="blues.4" py="15px" display="flex" flexWrap="wrap">
        <Box
          display="flex"
          alignItems="center"
          ml={isMobile ? "35px" : "185px"}
        >
          <SvgMoney />

          <Text ml="10px" fontSize={1} lineHeight="22px">
            {t("health.rangePrice", {
              0: min,
              1: max,
            })}
          </Text>
        </Box>

        <Box display="flex" alignItems="center" ml={isMobile ? "35px" : "25px"}>
          <SvgYearsOld />

          <Text ml="10px">
            {t("health.yearsOld", {
              0: age,
            })}
          </Text>
        </Box>
      </Box>

      <TariffCards tariffs={companiesTariffs} mt="6px" isHealth />

      <Footer mt={isMobile ? "50px" : "100px"} />
    </Box>
  )
}
