import React, { ReactElement, Fragment } from "react"
import { Box } from "./box"
import { theme } from "../theme/theme"
import { Text } from "./text"
import styled from "styled-components"
import { Button } from "./buttons/button"
import { t } from "../i18n/i18n"
import { CompaniesTariffCarHeader } from "./tariff-card-header"
import { TariffCardHealthHeader } from "./tariff-card-health-header"
import { TariffCardContent } from "./tariff-card-content"
import { Indexer } from "../core"
import { useHistory } from "react-router-dom"
import { healthForm } from "../healthcare/routes"

interface Props {
  isHealth?: boolean
  imagePath: string
  rating: number
  fields: Indexer
  total: string
}

export const Delimiter = styled(Box)`
  border-bottom: ${`${theme("borders.2")} ${theme("colors.greys.0")}`};
  opacity: 0.44;
`

export const TariffCard = ({
  isHealth,
  imagePath,
  rating,
  fields,
  total,
}: Props): ReactElement => {
  const history = useHistory()

  const handleClick = (): void => {
    history.push(healthForm, {
      nameInsurance: "Comprehensive health insurance PTI",
      packageName: "Platinum package",
      years: "16 - 35 years old",
      options: Object.keys(fields),
      total,
    })
  }

  return (
    <Box
      boxShadow="card"
      borderRadius="15px"
      minWidth={["310px", "344px"]}
      pb="30px"
      mx="7px"
      mt="30px"
    >
      <CompaniesTariffCarHeader imagePath={imagePath} rating={rating} />

      <Delimiter />

      {isHealth && (
        <Fragment>
          <TariffCardHealthHeader />

          <Delimiter />
        </Fragment>
      )}

      <TariffCardContent fields={fields} />

      <Text fontWeight="bold" fontSize={4} lineHeight="32px" textAlign="center">
        {total}
      </Text>

      <Button
        text={t("general.select")}
        variant="result"
        width="144px"
        mx="auto"
        display="flex"
        justifyContent="center"
        onClick={handleClick}
      />
    </Box>
  )
}
