import React, { ReactElement } from "react"
import { Box } from "./box"
import { Text } from "./text"

interface Props {
  fields: any
}

export const TariffCardContent = ({ fields }: Props): ReactElement => (
  <Box px="20px" py="10px">
    {Object.entries(fields).map(
      (item): ReactElement => (
        <Box
          key={item[0]}
          display="flex"
          justifyContent="space-between"
          alignItems="center"
          my="20px"
        >
          <Text
            fontSize={0}
            lineHeight="14px"
            my={0}
            maxWidth="110px"
            color="greys.4"
          >
            {item[0]}
          </Text>

          <Text fontWeight="bold" fontSize={0} lineHeight="14px" my={0}>
            {String(item[1])}
          </Text>
        </Box>
      ),
    )}
  </Box>
)
