import React, { ReactElement } from "react"
import { Box } from "./box"
import { Text } from "./text"
import { t } from "../i18n/i18n"
import { SvgStar } from "./icons/star"
import { SvgStarFilled } from "./icons/star-filled"

interface Props {
  imagePath: string
  rating: number
}

export const CompaniesTariffCarHeader = ({
  imagePath,
  rating,
}: Props): ReactElement => {
  const flooredRating = Math.floor(rating)

  return (
    <Box
      display="flex"
      justifyContent="space-between"
      pl="15px"
      pt="14px"
      pr="20px"
      pb="10px"
    >
      <img src={imagePath} alt="" />

      <Box>
        {[1, 2, 3, 4, 5].map(
          (item): ReactElement =>
            item <= flooredRating ? (
              <SvgStarFilled key={item} />
            ) : (
              <SvgStar key={item} />
            ),
        )}

        <Text
          my={0}
          color="blacks.1"
          fontSize="10px"
          lineHeight="12px"
          textAlign="right"
        >
          {t("general.points", {
            0: rating,
          })}
        </Text>
      </Box>
    </Box>
  )
}
